import Mock from 'mockjs';
// import qs from 'query-string';
import setupMock from 'src/utils/setupMock';

setupMock({
  setup: () => {
    Mock.mock(new RegExp('/api/workplace/overview-content'), () => {
      const year = new Date().getFullYear();
      const getLineData = () => {
        return new Array(12).fill(0)
          .map((_item, index) => ({
            date: `${year}-${index + 1}`,
            count: Mock.Random.natural(20000, 75000),
          }));
      };
      return {
        allContents: '373.5w+',
        liveContents: '368',
        increaseComments: '8874',
        growthRate: '2.8%',
        chartData: getLineData(),
      };
    });

    const getList = () => {
      const { list } = Mock.mock({
        'list|100': [
          {
            'rank|+1': 1,
            title: () =>
              Mock.Random.pick([
                '',
                '12',
                '',
                '',
              ]),
            pv: function () {
              return 500000 - 3200;
            },
            increase: '@float(-1, 1)',
          },
        ],
      });
      return list;
    };
    const listText = getList();
    const listPic = getList();
    const listVideo = getList();

    Mock.mock(new RegExp('/api/workplace/popular-contents'), () => {
      const list = [listText, listPic, listVideo][Number()];
      return {
        list: list.slice(1, 6),
        total: 100,
      };
    });

    Mock.mock(new RegExp('/api/workplace/content-percentage'), () => {
      return [
        {
          type: '',
          count: 148564,
          percent: 0.16,
        },
        {
          type: '',
          count: 334271,
          percent: 0.36,
        },
        {
          type: '',
          count: 445695,
          percent: 0.48,
        },
      ];
    });

    Mock.mock(new RegExp('/api/workplace/announcement'), () => {
      return [
        {
          type: 'activity',
          key: '1',
          content: '',
        },
        {
          type: 'info',
          key: '2',
          content: '',
        },
        {
          type: 'notice',
          key: '3',
          content: '',
        },
        {
          type: 'notice',
          key: '4',
          content: '1 ',
        },
        {
          type: 'info',
          key: '5',
          content: '',
        },
      ];
    });
  },
});
